import axios from 'axios'
import React, { useEffect, useState } from 'react'
import AdvrtSuccessScreen from '../../AdvrtSuccessScreen'
import { toast, ToastContainer } from 'react-toastify'
import HoardingForm1 from './HoardingForm1'
import HoardingFormDoc from './HoardingFormDoc'
import HoardingReview from './HoardingReview'
import AdvertisementApiList from '../../../AdvertisementApiList'
import ApiHeaderAdvrt from '../../../ApiHeaderAdvrt'
import { useParams } from 'react-router-dom'
import BrandLoader from '../../../../../Components/Common/BrandLoader'
import CommonModal from '../../../../../Components/GlobalData/CommonModal'
import ServerErrorCard from '../../../ServerErrorCard'
function HoardingIndex() {

    const { ulbId, applicationId, type } = useParams();

    console.log("ulbid in param", ulbId)
    console.log("application id in param", applicationId)
    console.log("type in param", type)
    const [formIndex, setFormIndex] = useState(1) //formindex specifies type of form  at index 1 ...
    const [allFormData, setAllFormData] = useState({})
    const [responseScreen, setresponseScreen] = useState()
    const [reviewData, setreviewData] = useState({})
    const [show, setshow] = useState(false)
    const [existingApplicationDetails, setexistingApplicationDetails] = useState()
    const [applicationType, setapplicationType] = useState()
    const [isLoading, setisLoading] = useState(false);
    const [erroState, seterroState] = useState(false);


    const showLoader = (val) => {
        setshow(val);
    }

    const { api_postHoardingApplication, api_renewHoarding_license, api_getHoardingApplicationDetailsToRenew } = AdvertisementApiList()

    //activating notification if no owner or no floor added
    const notify = (toastData, type) => {
        toast.dismiss();
        if (type == 'success') {
            toast.success(toastData)
        }
        if (type == 'error') {
            toast.error(toastData)
        }
    };

    const collectAllFormData = (key, formData, reviewIdName) => {
        console.log('prev of all Data', allFormData)
        console.log("review name by id in index...", reviewIdName)
        setAllFormData({ ...allFormData, [key]: formData })

        if (key == 'hoarding1') {
            console.log("data collecting by key", key, 'formData', formData, 'reviewData', reviewIdName)
            setreviewData({ ...reviewData, [key]: reviewIdName })
        }
        else {
            console.log('data not in review ===', key, '===', formData, 'preview...', reviewIdName)
            setreviewData({ ...reviewData, [key]: formData })
        }
    }
    console.log("all form data in index", allFormData)
    console.log("all form data in index for doc", allFormData?.selfAdvertisementDoc)

    ///// SUBMIT FORM /////
    const submitButtonToggle = () => {
        console.log('final form ready to submit...', allFormData)
        submitHoardingApplication()
    }

    const submitHoardingApplication = () => {
        setisLoading(true)
        let url
        let requestBody
        if (type == 'newapply') {
            url = api_postHoardingApplication  //new apply
            requestBody = {
                ulbId: ulbId,
                zoneId: allFormData?.hoarding1?.zone,
                licenseYear: allFormData?.hoarding1?.lisenseYear,
                HordingType: allFormData?.hoarding1?.hoardingCategory,
                displayLocation: allFormData?.hoarding1?.location,
                width: allFormData?.hoarding1?.widthOfHoarding,
                length: allFormData?.hoarding1?.lengthOfHoarding,
                displayArea: allFormData?.hoarding1?.areaOfBoard,
                longitude: allFormData?.hoarding1?.longitude,
                latitude: allFormData?.hoarding1?.latitude,
                material: allFormData?.hoarding1?.materialType,
                illumination: allFormData?.hoarding1?.illumination,
                indicateFacing: allFormData?.hoarding1?.facing,
                propertyType: allFormData?.hoarding1?.propertyType,
                displayLandMark: allFormData?.hoarding1?.landmark,
                propertyOwnerName: allFormData?.hoarding1?.ownerName,
                propertyOwnerAddress: allFormData?.hoarding1?.ownerAddress,
                propertyOwnerCity: allFormData?.hoarding1?.ownerCity,
                propertyOwnerWhatsappNo: allFormData?.hoarding1?.ownerWhatsappNo,
                propertyOwnerMobileNo: allFormData?.hoarding1?.ownerPhone,
                documents: allFormData?.hoardingDoc,
            }
        }
        if (type == 'renew') {
            url = api_renewHoarding_license  //new apply
            requestBody = {
                applicationId: applicationId,
                ulbId: ulbId,
                zoneId: allFormData?.hoarding1?.zone,
                licenseYear: allFormData?.hoarding1?.lisenseYear,
                HordingType: allFormData?.hoarding1?.hoardingCategory,
                displayLocation: allFormData?.hoarding1?.location,
                width: allFormData?.hoarding1?.widthOfHoarding,
                length: allFormData?.hoarding1?.lengthOfHoarding,
                displayArea: allFormData?.hoarding1?.areaOfBoard,
                longitude: allFormData?.hoarding1?.longitude,
                latitude: allFormData?.hoarding1?.latitude,
                material: allFormData?.hoarding1?.materialType,
                illumination: allFormData?.hoarding1?.illumination,
                indicateFacing: allFormData?.hoarding1?.facing,
                propertyType: allFormData?.hoarding1?.propertyType,
                displayLandMark: allFormData?.hoarding1?.landmark,
                propertyOwnerName: allFormData?.hoarding1?.ownerName,
                propertyOwnerAddress: allFormData?.hoarding1?.ownerAddress,
                propertyOwnerCity: allFormData?.hoarding1?.ownerCity,
                propertyOwnerWhatsappNo: allFormData?.hoarding1?.ownerWhatsappNo,
                propertyOwnerMobileNo: allFormData?.hoarding1?.ownerPhone,
                documents: allFormData?.hoardingDoc,
            }

        }

        console.log('request body...', requestBody)
        axios.post(`${url}`, requestBody, ApiHeaderAdvrt())
            .then(function (response) {
                console.log('response after data submitted', response.data.data)
                setresponseScreen(response.data.data)
                setisLoading(false)
                notify('submitted successfully', 'success')
            })
            .catch(function (error) {
                console.log('errorrr.... ', error);
                setisLoading(false)
                notify('failed to submit', 'error')
            })
    }

    // passing values in components
    const values = {
        setFormIndex: setFormIndex,
        showLoader: showLoader,
        collectFormDataFun: collectAllFormData,
        toastFun: notify,
        id: ulbId,
        type: type,
        existingApplicationDetails: existingApplicationDetails,
    }
    useEffect(() => {
        setapplicationType(type)
        if (type == 'renew') {
            fetchApplicationDetails()
        }
    }, [])
    const fetchApplicationDetails = () => {
        seterroState(false)
        setisLoading(true)
        let requestBody = {
            applicationId: applicationId,
        }

        console.log('renew application id', requestBody)
        axios.post(`${api_getHoardingApplicationDetailsToRenew}`, requestBody, ApiHeaderAdvrt())
            .then(function (response) {
                console.log('getting application detail for renew application.....', response.data.data)
                setexistingApplicationDetails(response.data.data)
                setisLoading(false)
            })
            .catch(function (error) {
                console.log('==2 details by id error...', error)
                setisLoading(false)
            })
    }

    console.log("application details for renew", existingApplicationDetails)

    console.log("response screen", responseScreen)
    if (responseScreen?.status == true) {
        return (
            <>
                <AdvrtSuccessScreen responseScreenData={responseScreen} />
            </>
        )
    }
    if (isLoading) {
        return (
            <>
                <BrandLoader />
                <div className='min-h-screen'></div>
            </>
        )
    }
    if (erroState) {
        return (
            <CommonModal>
                <ServerErrorCard title="Server is busy" desc="Server is too busy to respond. Please try again later." buttonText="View Dashboard" buttonUrl="/advertisement-dashboard" />
            </CommonModal>
        )
    }

    return (
        <>

            <ToastContainer position="top-right"
                autoClose={2000} />
            <div className='overflow-x-clip  p-4 mt-3 container mx-auto h-screen'>
                <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-2 bg-white p-2 rounded-md shadow-md shadow-violet-200  '>
                    <div className=''>
                        <div className='flex flex-row '>
                            <h1 className='text-2xl ml-4 text-gray-600 font-sans font-semibold'>Hoarding Registration</h1>
                        </div>
                        <h1 className='text-xs ml-3 p-1 text-gray-600 font-sans'>
                            {/* You Can Get License To Advertise Your Business Name On Your Shop */}
                        </h1>
                    </div>
                    <div>
                        <div className='flex flex-row mt-2 float-right'>
                            <span className='text-md font-bold md:text-xl text-violet-600 text-center  transition-all animate-wiggle'>&emsp; <strong className='text-2xl text-violet-600'>{3 - formIndex}
                            </strong> more screen</span>
                            <img src='https://cdn-icons-png.flaticon.com/512/1684/1684121.png' className='h-10 mr-4  opacity-80 float-right  ml-4' />
                        </div>
                    </div>
                </div>
                <div className={`${formIndex == 1 ? 'translate-x-0' : 'translate-x-full'} transition-all`}><HoardingForm1 values={values} /></div>
                {/* <div className={`${formIndex == 2 ? 'translate-x-0' : 'translate-x-full'} transition-all`}><HoardingForm2 values={values} /></div>
                <div className={`${formIndex == 3 ? 'translate-x-0' : 'translate-x-full'} transition-all `}><HoardingForm3 values={values} /></div> */}
                <div className={`${formIndex == 2 ? 'translate-x-0' : 'translate-x-full'} transition-all `}><HoardingFormDoc values={values} /></div>
                <div className={`${formIndex == 3 ? 'translate-x-0' : 'translate-x-full'} transition-all `}><HoardingReview values={values} reviewIdNameData={reviewData} allFormData={allFormData} submitFun={submitButtonToggle} /></div>

            </div>
        </>
    )
}

export default HoardingIndex