import React from 'react'
import { nullToNA } from '../../../../../Components/Common/PowerUps/PowerupFunctions'

function HoardingReview(props) {

    const { setFormIndex, showLoader, collectFormDataFun, toastFun } = props?.values

    let labelStyle = "mt-6 -ml-7 text-md text-gray-600  font-bold"
    let inputStyle = "mt-6 -ml-7 text-sm text-gray-600"

    const submitForm = () => {
        props.submitFun()
    }
    console.log("data in review form...", props?.reviewIdNameData)
    console.log("data in review form documents...", props?.reviewIdNameData)

    return (
        <>
            <div className='absolute w-full top-4 ml-4 '>
                <div className='grid grid-cols-1 md:grid-cols-12 lg:grid-cols-12 gap-4 container mx-auto  '>
                    <div className='col-span-8 border border-dashed border-violet-800 '>
                    <h1 className='text-center p-3 mb-2 text-lg bg-white text-gray-600 font-sans font-bold'>Application Details</h1>
                        <div className='grid grid-cols-1 md:grid-cols-4 lg:grid-cols-4 p-2 ml-12'>
                            <div className=''>
                                <p className={`${labelStyle}`}>License Year</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.hoarding1?.lisenseYear || props?.reviewIdNameData?.hoarding1?.license_year)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Hoarding Category </p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.hoarding1?.hoardingCategory || props?.reviewIdNameData?.hoarding1?.typology_name)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Longitude </p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.hoarding1?.longitude || props?.reviewIdNameData?.hoarding1?.longitude)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Latitude </p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.hoarding1?.latitude || props?.reviewIdNameData?.hoarding1?.latitude)}</span>
                            </div>
                        </div>
                        <div className='grid grid-cols-1 md:grid-cols-4 lg:grid-cols-4 p-2 ml-12'>
                            <div className=''>
                                <p className={`${labelStyle}`}>Location </p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.hoarding1?.location || props?.reviewIdNameData?.hoarding1?.location)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Length of Hoarding</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.hoarding1?.lengthOfHoarding || props?.reviewIdNameData?.hoarding1?.length)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Width of Hoarding</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.hoarding1?.widthOfHoarding || props?.reviewIdNameData?.hoarding1?.width)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Area of Board</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.hoarding1?.areaOfBoard || props?.reviewIdNameData?.hoarding1?.area)}</span>
                            </div>
                        </div>
                        <div className='grid grid-cols-1 md:grid-cols-4 lg:grid-cols-4 p-2 ml-12'>
                            <div className=''>
                                <p className={`${labelStyle}`}>Landmark</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.hoarding1?.landmark || props?.reviewIdNameData?.hoarding1?.landmark)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Facing </p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.hoarding1?.facing || props?.reviewIdNameData?.hoarding1?.facing)}</span>
                            </div>

                            <div className=''>
                                <p className={`${labelStyle}`}>Property Type</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.hoarding1?.propertyType || props?.reviewIdNameData?.hoarding1?.property_type)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Zone </p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.hoarding1?.zone || props?.reviewIdNameData?.hoarding1?.zone_id)}</span>
                            </div>
                        </div>
                        <div className='grid grid-cols-1 md:grid-cols-3 lg:grid-cols-3 p-2 ml-12'>
                            <div className=''>
                                <p className={`${labelStyle}`}>Owner Name(If property type is private)</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.hoarding1?.ownerName || props?.reviewIdNameData?.hoarding1?.property_owner_name)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Owner Address(If property type is private)</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.hoarding1?.ownerAddress || props?.reviewIdNameData?.hoarding1?.property_owner_address)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Owner City(If property type is private) </p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.hoarding1?.ownerCity || props?.reviewIdNameData?.hoarding1?.property_owner_city)}</span>
                            </div>
                        </div>
                        <div className='grid grid-cols-1 md:grid-cols-3 lg:grid-cols-3 p-2 ml-12'>
                            <div className=''>
                                <p className={`${labelStyle}`}>Owner Phone(If property type is private)</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.hoarding1?.ownerPhone || props?.reviewIdNameData?.hoarding1?.property_owner_mobile_no)}</span>
                            </div><div className=''>
                                <p className={`${labelStyle}`}>Owner Whatsapp No.(If property type is private)</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.hoarding1?.ownerWhatsappNo || props?.reviewIdNameData?.hoarding1?.property_owner_whatsapp_no)}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Material Type </p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.hoarding1?.materialType || props?.reviewIdNameData?.hoarding1?.material)}</span>
                            </div>
                        </div>
                        <div className='grid grid-cols-1 md:grid-cols-4 lg:grid-cols-4 p-2 ml-12'>
                            <div className=''>
                                <p className={`${labelStyle}`}>Illumination  </p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.hoarding1?.illumination || props?.reviewIdNameData?.hoarding1?.illumination_name)}</span>
                            </div>
                        </div>

                    </div>
                    {/* document */}
                    <div className='col-span-4 border border-dashed border-violet-800 '>
                        <h1 className='text-center p-3 mb-2 text-lg bg-white text-gray-600 font-sans font-bold'>Documents Uploaded</h1>
                        <div className='grid grid-cols-1 md:grid-cols-1 lg:grid-cols-1 p-2 ml-12'>
                            {props?.reviewIdNameData?.hoardingDoc?.map((data, index) => (
                                <div className=''>
                                    <>
                                        <p className={`${labelStyle}`}>{data?.docCode} </p>
                                        <div className='flex -mt-2'>
                                            <span className={`${inputStyle} flex-1`}>{data?.image?.name}</span>
                                            <span className={`mt-4  flex-1 float-right`}><img src="https://cdn-icons-png.flaticon.com/512/1829/1829589.png" alt="Preview Image" className={` w-8`} /></span>
                                        </div>
                                    </>
                                </div>
                            ))}

                        </div>
                    </div>
                </div>

                <div className="grid grid-cols-12 w-full p-3">
                    <div className='md:pl-0 col-span-6'>
                        <button type="button" class="text-xs py-2 px-4 inline-block text-center mb-3 rounded leading-5 text-gray-100 bg-indigo-500 border border-indigo-500 hover:text-white hover:bg-indigo-600 hover:ring-0 hover:border-indigo-600 focus:bg-indigo-600 focus:border-indigo-600 focus:outline-none focus:ring-0" onClick={() => setFormIndex(2)}>Back</button>
                    </div>
                    <div className='col-span-6'>
                        <button type="submit" class="float-right text-xs py-2 px-4 inline-block text-center mb-3 rounded leading-5 text-gray-100 bg-green-500 border border-green-500 hover:text-white hover:bg-green-600 hover:ring-0 hover:border-green-600 focus:bg-green-600 focus:border-green-600 focus:outline-none focus:ring-0" onClick={submitForm}>Submit</button>
                    </div>
                </div>
            </div>
        </>
    )
}

export default HoardingReview